import React from 'react';
import TweenLite from 'gsap';

function ScrollMe() {
  function downButton() {
    TweenLite.to(
      document.querySelector('.work-page__scroll'),
      0.5,
      {
        scrollTo: {
          y: window.innerHeight,
        },
      },
    );
  }
  return (
    <button className="scroll-me" type="button" title="Scroll Me" onClick={downButton}>
      <span className="mouse"><span /></span>
      <p>scroll me</p>
    </button>

  );
}

export default ScrollMe;



